import React, { createContext, useState, useContext, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { AuthContext } from '../auth/auth.context'
import { LoadingModalContext } from '../loading-modal/loading-modal.contenxt'

import {
    fetchUsers,
    fetchUser,
    patchUser,
    postAdmin,
    postUser,
} from './user.queries'

import {
    initUserObj
} from './user.utils'

export const UserContext = createContext({
    isUpdating: false,
    activeIndex: 0,
    handleChangeFilterItem: () => { },
    filterItems: [],
    totalCount: 0,
    users: null,
    user: null,
    getUsers: () => { },
    getUser: () => { },
    handleChange: () => { },
    updateUser: () => { },
    resetUser: () => { },
    formToShow: 0,
    switchFormButtons: [],
    toggleUserForm: () => { },
    createUser: () => { },
    getFilteredUsers: () => { },
    getUserByQuery: () => { }
})

const UserProvider = ({ children }) => {
    const { push } = useHistory()
    const { token } = useContext(AuthContext)
    const {
        closeModal,
        getMessage,
        setIsLoading,
        setShowModal
    } = useContext(LoadingModalContext)

    // ------------------------

    const [user, setUser] = useState(initUserObj)
    const [users, setUsers] = useState(null)
    const [totalCount, setTotalCount] = useState(0)

    // ------------------------

    const [isUpdating, setIsUpdating] = useState(false)
    const [activeIndex, setActiveIndex] = useState(0)
    const filterItems = [
        {
            id: 0,
            name: "Zákaznici",
            filter: 0,
        },
        {
            id: 1,
            name: "Zamestnanci",
            filter: 1,
        },
        {
            id: 2,
            name: "Vedúci",
            filter: 2,
        },
        // {
        //     id: 3,
        //     name: "Velký brat",
        //     filter: 3,
        // }
    ]

    // ------------------------

    const [formToShow, setFormToShow] = useState(0)
    const switchFormButtons = [
        {
            name: "Zákazník"
        },
        {
            name: "Admin"
        }
    ]

    const toggleUserForm = (e, idx) => {
        e.preventDefault()
        setFormToShow(idx)
    }

    // ------------------------


    const handleChangeFilterItem = (itemIndex) => {
        setActiveIndex(itemIndex)
    }

    const handleChange = (e) => {
        const { name, value } = e.target
        setUser({
            ...user,
            [name]: value
        })
    }

    const handleParameterChange = (e, idx) => {
        const { name, value } = e.target
        let arr = user.lenses[name]
        arr[idx] = Number(value)
        setUser({
            ...user,
            lenses: {
                ...user.lenses,
                [name]: arr
            }
        })
    }

    useEffect(() => {
        setUser({
            ...user,
            name: user.fName + " " + user.lName
        })
    }, [user.fName, user.lName])

    const resetUser = () => {
        setUser({
            ...initUserObj
        })
        setIsUpdating(false)
        setFormToShow(0)
    }

    // ------------------------
    // USER ACTIONS
    // ------------------------

    // Get all users
    const getUsers = async () => {
        setIsLoading(true)
        setShowModal(true)

        try {
            const response = await fetchUsers(token)
            const data = await response.json()

            console.log(data)
            if (data.users) {
                setUsers(data.users)
                setTotalCount(data.count)
                setIsLoading(false)
                closeModal()

            } else {
                getMessage(data.message)
                setIsLoading(false)
            }
        } catch (err) {
            console.log(err)
            getMessage("Nieco sa pokazilo")
            setIsLoading(false)
        }
    }

    // Get single user
    const getUser = async (id) => {
        setIsLoading(true)
        setShowModal(true)
        setIsUpdating(true)

        try {
            const response = await fetchUser(token, id)
            const data = await response.json()

            if (data.user) {
                setUser({
                    ...user,
                    ...data.user,
                    lenses: {
                        // cylinder: [0, 0, 0, 0],
                        // cylinderAxes: [0, 0, 0, 0],
                        // diopters: [0, 0, 0, 0],
                        // distance: [0, 0, 0, 0],
                        // addition: [0, 0, 0, 0],
                        // basis: [0, 0, 0, 0],
                        // prism: [0, 0, 0, 0],
                        cylinder: data.user.lenses.cylinder.length ? data.user.lenses.cylinder : [0, 0, 0, 0],
                        cylinderAxes: data.user.lenses.cylinderAxes.length ? data.user.lenses.cylinderAxes : [0, 0, 0, 0],
                        diopters: data.user.lenses.diopters.length ? data.user.lenses.diopters : [0, 0, 0, 0],
                        distance: data.user.lenses.distance.length ? data.user.lenses.distance : [0, 0, 0, 0],
                        addition: data.user.lenses.addition.length ? data.user.lenses.addition : [0, 0, 0, 0],
                        basis: data.user.lenses.basis.length ? data.user.lenses.basis : [0, 0, 0, 0],
                        prism: data.user.lenses.prism.length ? data.user.lenses.prism : [0, 0, 0, 0],
                    },
                    fName: data.user.name ? data.user.name.split(" ")[0] : "",
                    lName: data.user.name ? data.user.name.split(" ")[1] : ""
                })
                setIsLoading(false)
                closeModal()

            } else {
                getMessage(data.message)
                setIsLoading(false)
            }
        } catch (err) {
            console.log(err)
            getMessage("Nieco sa pokazilo")
            setIsLoading(false)
        }
    }

    // Update user
    const updateUser = async (user) => {
        setIsLoading(true)
        setShowModal(true)

        console.log(user)

        try {
            const response = await patchUser(token, user)
            const data = await response.json()

            console.log(data)

            if (data.error) {
                getMessage(data.message)
                if (data.error === 'format' && data.message.includes('lenses.cylinderAxes')) {
                    getMessage("Osa musí byt väčšia alebo rovná 0")
                }
                if (data.error === 'phone-exists') {
                    getMessage("Telefónne číslo už v databázy existuje")
                }
                setIsLoading(false)
                return
            }


            setIsLoading(false)
            closeModal()
            push('/dashboard/zakaznici')
            getUsers()
            // http://localhost:1000/#/dashboard/zakaznici


        } catch (err) {
            console.log(err)
            getMessage("Nieco sa pokazilo")
            setIsLoading(false)
        }
    }

    const createUser = async (user) => {
        setIsLoading(true)
        setShowModal(true)

        try {
            if (formToShow === 0) {
                let data = {}
                if (!user._id) {
                    console.log('creating new user')
                    const response = await postUser(token, user)

                    data = await response.json()

                    console.log(data)

                    if (data.error) {
                        getMessage(data.message)

                        if (data.type === 'email') {
                            getMessage("Email existuje")
                        }
                        if (data.error === 'format' && data.message.includes('email')) {
                            getMessage("Email musi byt platny email")
                        }
                        setIsLoading(false)
                        return
                    }

                    setUser({
                        ...user,
                        _id: data.user._id
                    })
                }

                updateUser({
                    _id: data.user ? data.user._id : user._id,
                    lenses: {
                        ...user.lenses
                    },
                    address: user.city,
                    city: user.city,
                    country: user.country,
                    phone: user.phone,
                    psc: user.phone,
                })
                // setIsLoading(false)
                // getUsers()
                // push('/dashboard/zakaznici')
                // closeModal()

            } else {
                const response = await postAdmin(token, user)
                const data = await response.json()

                console.log(data)

                if (data.error) {
                    getMessage(data.message)
                    setIsLoading(false)
                    return
                }

                setIsLoading(false)
                getUsers()
                push('/dashboard/zakaznici')
                closeModal()
            }

        } catch (err) {
            console.log(err)
            getMessage("Nieco sa pokazilo")
            setIsLoading(false)
        }
    }

    // ----------------------------------------------
    // ----------------------------------------------
    // ----------------------------------------------

    const myHeaders = new Headers();
    myHeaders.append("auth-token", token);
    myHeaders.append("Content-Type", "application/json");

    const getFilteredUsers = async (filter) => {
        setIsLoading(true)
        setShowModal(true)

        console.log(filter)
        const raw = JSON.stringify(filter)

        const requestOptions = {
            method: 'POST',
            headers: myHeaders,
            body: raw,
            redirect: 'follow'
        };

        try {
            const response = await fetch(`${process.env.REACT_APP_BACKEND_ENDPOINT}/api/admin/users/filter`, requestOptions)
            const data = await response.json()

            console.log(data)
            if (data.users) {
                setUsers(data.users)
                closeModal()
                return
            }

            getMessage(data.message)
            setIsLoading(false)

        } catch (err) {
            console.log(err)
            getMessage("Nieco sa pokazilo")
            setIsLoading(false)
        }
    }

    const getUserByQuery = async (query) => {
        setIsLoading(true)
        setShowModal(true)

        console.log(query)
        const raw = JSON.stringify(query)

        const requestOptions = {
            method: 'POST',
            headers: myHeaders,
            body: raw,
            redirect: 'follow'
        };

        try {
            const response = await fetch(`${process.env.REACT_APP_BACKEND_ENDPOINT}/api/admin/users/filter`, requestOptions)
            const data = await response.json()

            console.log(data)
            if (data.users) {
                setUsers(data.users)
                closeModal()
                return
            }

            getMessage(data.message)
            setIsLoading(false)

        } catch (err) {
            console.log(err)
            getMessage("Nieco sa pokazilo")
            setIsLoading(false)
        }
    }

    return (
        <UserContext.Provider
            value={{
                isUpdating,
                activeIndex,
                handleChangeFilterItem,
                filterItems,
                users,
                user,
                totalCount,
                getUsers,
                getUser,
                handleChange,
                handleParameterChange,
                updateUser,
                resetUser,
                formToShow,
                switchFormButtons,
                toggleUserForm,
                createUser,
                getFilteredUsers,
                getUserByQuery
            }}
        >
            {children}
        </UserContext.Provider>
    )
}

export default UserProvider